@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: "Montserrat", sans-serif;
}

p {
  @apply mb-4
}

.calli {
  font-family: 'Pacifico', cursive;
}

#nav_menu > li{
  @apply float-left ml-3 text-xl;
}

.layout {
  @apply px-4 md:px-8 lg:px-16 xl:px-32 py-2;
}

.layout_more_space {
  @apply px-4 md:px-8 lg:px-16 xl:px-32 py-16;
}
.portrait_image {
  max-height: 500px;
  @apply w-auto h-auto rounded shadow-xl;
}

.shaped_top_left {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.shaped_top_left svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 45px;
  /* transform: rotateY(180deg); */
}

.shaped_top_left .shape-fill {
  fill: #FFFFFF;
}

.shape-divider-bottom-right {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.shape-divider-bottom-right svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 30px;
  transform: rotateY(180deg);
}

.shape-divider-bottom-right .shape-fill {
  fill: #FFFFFF;
}